import React from 'react';
import './Image.css';

class Image extends React.Component {

    constructor(props) {
        super(props);
        console.log("image constructor");
    }

    render() {
      return (
        <div>
          <img 
            className="sizing"
            src={require('./images/'+this.props.imageName)}
            />
        </div>
      );
    }

}

export default Image;
