import React from 'react';
import './Status.css';

class Status extends React.Component {

    constructor(props) {
        super(props);
        console.log("status constructor");
    }

    render() {
      return (
        <div className="status">
            <div className="statusmini">
                {this.props.hint}
            </div>
            <img 
                className="statusimage"
                src={require('./images/'+this.props.imageName)}
            />
        </div>
      );
    }

}

export default Status;
